import * as React from 'react'
import Highlight, { defaultProps } from 'prism-react-renderer'
import theme from 'prism-react-renderer/themes/nightOwl'

import { styled, Box, Row, Text, font } from '@flame-ui/react'

import { UserPrefs, Preferences } from '../../contexts/UserPrefs'
import { copyToClipboard } from '../../utils/copyToClipboard'

import { transparentize } from 'polished'

const Pre = styled.pre`
  text-align: left;
  margin: 1em 0;
  padding: 1.5em;
  overflow: scroll;
  font-size: 18px;
  border-radius: 6px;
  background-color: #152a4f;
  margin-bottom: 32px;
  color: white;
  font-family: ${font('code')};
  line-height: 24px;
  *::selection {
    background-color: #304667;
  }
`

const Line = styled.div`
  display: table-row;
`

const LineContent = styled.span`
  display: table-cell;
`

const CopyButton = styled.button`
  border-radius: 3px;
  padding: 6px 8px;
  &:hover {
    background-color: rgba(256, 256, 256, 0.2);
  }
`

const colors = {
  yarn: '#2188b6',
  npm: '#cb3837',
  js: '#f7df1e',
  ts: '#3178c6',
  bash: '#ffffff',
  html: '#ff8952',
}

const Variant: React.FC<{ type: string; isActive: boolean }> = ({
  type,
  isActive = false,
  children,
}) => {
  const color = type ? colors[type as keyof typeof colors] : '#FFFFFF'
  return (
    <Box
      bg={isActive ? transparentize(0.8, color) : 'transparent'}
      color={color}
      minWidth="48px"
      px={2}
      py={1}
      borderRadius={3}
    >
      <Text fontFamily="primary" fontSize="caption">
        {children}
      </Text>
    </Box>
  )
}

const Code: React.FC<{ children: any }> = ({ children }) => {
  const { prefs, setPrefs } = React.useContext(UserPrefs)

  const className = children.props.className || ''
  const matches = className.match(/language-(?<lang>.*)/)
  const lang = matches && matches.groups && matches.groups.lang ? matches.groups.lang : ''

  const codeString = children.props.children as string

  const isVariable = Boolean(children.props.metastring)

  let type = lang

  let variants: { [key: string]: string } = {}

  const changeHandler = (key: string) => setPrefs({ [type]: key })

  if (isVariable) {
    const strings = codeString.split('---')
    const meta = children.props.metastring.split(':')
    type = meta[0]
    const _keys = meta[1].split('-')
    _keys.forEach((key: string, idx: number) => {
      variants[key] = strings[idx] || codeString
    })
  }

  const variantsKeys = isVariable ? Object.keys(variants) : [lang]
  const current = isVariable ? prefs[type as keyof Preferences] : variantsKeys[0]

  return (
    <Highlight
      {...defaultProps}
      theme={theme}
      code={isVariable ? variants[current].trim() : codeString.trim()}
      language={lang}
    >
      {({ tokens, getLineProps, getTokenProps }) => (
        <Pre>
          <Row justifyContent="space-between" alignItems="center" mb={3}>
            <Row gap={2}>
              {variantsKeys.map((item: string) => (
                <button key={item} onClick={() => (isVariable ? changeHandler(item) : null)}>
                  <Variant isActive={item === current} type={item}>
                    {item.toUpperCase()}
                  </Variant>
                </button>
              ))}
            </Row>
            <CopyButton onClick={() => copyToClipboard(codeString)}>
              <Text fontSize="caption" color="white">
                COPY
              </Text>
            </CopyButton>
          </Row>
          {tokens.map((line, i) => (
            <Line key={i} {...getLineProps({ line, key: i })}>
              <LineContent>
                {line.map((token, key) => (
                  <span key={key} {...getTokenProps({ token, key })} />
                ))}
              </LineContent>
            </Line>
          ))}
        </Pre>
      )}
    </Highlight>
  )
}

export default Code
