import * as React from 'react'

export type Preferences = {
  pkg: 'yarn' | 'npm'
  lang: 'ts' | 'js'
}

export interface UserPrefs {
  prefs: Preferences
  setPrefs: (state: Partial<Preferences>) => void
}

export const UserPrefs = React.createContext<Partial<UserPrefs>>({
  prefs: {
    pkg: 'yarn',
    lang: 'ts',
  },
})

export const UserPrefsProvider: React.FC = ({ children }) => {
  const [state, setState] = React.useState<Preferences>({
    pkg: 'yarn',
    lang: 'ts',
  })
  const setPrefs = (value: Partial<Preferences>) => setState((prev) => ({ ...prev, ...value }))
  return <UserPrefs.Provider value={{ prefs: state, setPrefs }}>{children}</UserPrefs.Provider>
}
